<template>
    <div class="content">
        <div class="query">
            <div class="query-item">
                <i-select v-model="selectUserType" @on-change="handleChangeType" style="margin-left: 10px;width: 220px;">
                    <i-option v-for="(item, index) in userTypeList" :key="index" :value="item.value">{{item.title}}</i-option>
                </i-select>
                <i-input placeholder="搜索用户ID或者用户昵称" v-model="searchUsername" style="width: 160px;"></i-input>
                <div class="search" @click="handleSearch">搜索</div>
                <div class="member-num">当前注册会员：<span style="color: #ff9933;" v-text="memberNum"></span>（最大注册数200）</div>
            </div>
            <div class="query-poptip">
                <p>说明：整个后台的用户名默认显示用户昵称，如果在账户设置填写了用户备注，则用户名显示备注名，这样方便管理员识别会员玩家。</p>
            </div>
        </div>
        <div class="list">
            <div class="list-item list-header">
                <div class="item-info item-1">
                    <span>头像</span>
                </div>
                <div class="item-info item-2">
                    <span>用户名</span>
                </div>
                <div class="item-info item-3">
                    <span>ID</span>
                </div>
                <div class="item-info item-4">
                    <span>账户余额</span>
                </div>
                <div class="item-info item-5">
                    <span>今日盈亏</span>
                </div>
                <div class="item-info item-6">
                    <span>今日投注游戏</span>
                </div>
                <div class="item-info item-7">
                    <span>是否飞单</span>
                </div>
                <div class="item-info item-8">
                    <span>操作</span>
                </div>
            </div>
            <div class="list-content">
                <div class="list-item" v-for="(item, index) in userData" :key="index">
                    <div class="item-info item-1">
                        <img :src="item.header" @error="handleImageError(item)" alt title/>
                    </div>
                    <div class="item-info item-2">
                        <p>{{item.username}}</p>
                    </div>
                    <div class="item-info item-3">
                        <span>{{item.showId}}</span>
                    </div>
                    <div class="item-info item-4">
                        <span>{{item.balance}}</span>
                    </div>
                    <div class="item-info item-5">
                        <span>{{item.proLoss}}</span>
                    </div>
                    <div class="item-info item-6">
                        <span>{{item.betGameName || '无'}}</span>
                    </div>
                    <div class="item-info item-7">
                         <!-- v-show="item.status === '正常'" -->
                        <switchs :open="item.record" @switch="handleChangeFlyingSheet(item)"></switchs>
                    </div>
                    <div class="item-info item-8">
                        <div class="operate">
                            <span @click="handleScoreHistory(item)">上下分记录</span>
                            <span @click="handleAccountHistory(item)">账变记录</span>
                            <span :class="[item.status === '正常' ? '' : 'no-show']" @click="handleCollecAcction(item)">收款账户</span>
                            <span @click="handleChange(item)" :style="{cursor: item.isFalse ? 'default' : 'pointer'}">{{!item.isFalse ? (item.status === '假人' ? '切换真人' :'切换假人') : '机器人托'}}</span>
                            <span @click="handleFreezing(item)" :class="[(item.status === '正常' || item.status === '冻结') ? '' : 'no-show']">{{item.status !== '冻结' ? '冻结' : '解冻'}}</span>
                            <span @click="handleDelete(item)">删除</span>
                            <span :class="[item.status === '正常' ? '' : 'no-show']" @click="handleAgent(item)">{{item.isAgent ? '取消代理' : '设为代理'}}</span>
                        </div>
                        <span class="other-setting" @click="handleSettingUser(item)">账户设置</span>
                    </div>
                </div>
            </div>
        </div>
        <Page class="footer-page" :total="page.total" :page-size="page.pageSize" :current="page.pageNum" show-sizer @on-page-size-change="handleChangeSize" @on-change="handleChangePage"/>
        <div v-show="showUserInfo || showLotteryRecode || showBranchRecode || showAccount" class="modal-mask" :style="{'align-items': clientHeight < 800 ? 'flex-start' : 'center'}">
            <div v-show="showUserInfo" class="user-info user-setting" :style="{'height': userInfo.OpenID ? '550px' : '430px'}">
                <div class="user-info-header">
                    <span>用户信息</span>
                    <span class="modal-close" @click="handleCloseModal('showUserInfo')">X</span>
                </div>
                <div class="user-info-c">
                    <div class="user-top top-1">
                        <div class="user-info-h">
                            <img :src="imgDataUrl || userInfo.header" alt title/>
                            <p class="change-header" @click="showUploadHeader = !showUploadHeader">更改头像</p>
                        </div>
                        <div class="user-info-r">
                            <div class="usre-top-i">
                                <div class="user-info-c-i">
                                    <span class="user-info-c-i-l">昵称</span>
                                    <i-input v-model="userInfo.nickname" disabled class="user-info-c-i-r"></i-input>
                                </div>
                                <div class="user-info-c-i">
                                    <span class="user-info-c-i-l">ID</span>
                                    <i-input v-model="userInfo.showId" disabled class="user-info-c-i-r"></i-input>
                                </div>
                                <div class="user-info-c-i">
                                    <span class="user-info-c-i-l">{{userInfo.OpenID ? '登录方式' : '登录账号'}}</span>
                                    <i-input v-if="!userInfo.OpenID" v-model="userInfo.usrename" disabled class="user-info-c-i-r"></i-input>
                                    <i-input v-if="userInfo.OpenID" :value="'微信'" disabled class="user-info-c-i-r"></i-input>
                                </div>
                            </div>
                            <div class="usre-top-i">
                                <div class="user-info-c-i">
                                    <span class="user-info-c-i-l">余额</span>
                                    <i-input v-model="userInfo.accountBalance" disabled class="user-info-c-i-r"></i-input>
                                </div>
                                <div v-show="userInfo.status !== '假人'" class="user-info-c-i">
                                    <span class="user-info-c-i-l">最近一次登录</span>
                                    <i-input v-model="userInfo.loginTime" disabled class="user-info-c-i-r"></i-input>
                                </div>
                            </div>
                        </div>
                        <div v-if="userInfo.OpenID" class="user-info-c-i wechat-box">
                            <div class="grant-wechat">
                                <p class="wechat-item" v-html="`授权微信:&nbsp;&nbsp;${userInfo.OpenID}`"></p>
                                <p class="wechat-item" v-html="`登录账号:&nbsp;&nbsp;${userInfo.usrename || '无'}`"></p>
                                <div class="button" @click="handleVerifyAccount">生成登录账号</div>
                            </div>
                            <div class="wechat-poptip">
                                <p>1.如果客户是微信登录，可以在此给他生成登录账号，绑定数据，方便账号登录，默认密码888888。</p>
                                <p>2.如果客户是账号登录，需要绑定微信授权，则客户可以通过微信登录后，在个人信息页面自助绑定。</p>
                                <p>3.已绑定过的微信或者账号无法解绑或者再次绑定。</p>
                            </div>
                        </div>
                    </div>
                    <div class="user-top">
                        <div class="list-header">
                            <div class="list-title">
                                <span>账户设置/修改</span>
                            </div>
                        </div>
                        <div class="user-top-item">
                            <div class="user-info-c-i">
                                <p class="user-info-c-i-l">用户发言</p>
                                <i-select v-model="userInfo.speakingStatus" class="user-info-c-i-r">
                                    <i-option v-for="(item, index) in userSpeakList" :key="index" :value="item.status">{{item.title}}</i-option>
                                </i-select>
                            </div>
                            <div class="user-info-c-i">
                                <p class="user-info-c-i-l">彩票回水方案</p>
                                <i-select v-model="userInfo.level" class="user-info-c-i-r">
                                    <i-option v-for="(item, index) in userLevelList" :key="index" :value="item.value">{{item.title}}</i-option>
                                </i-select>
                            </div>
                            <div class="user-info-c-i">
                                <p class="user-info-c-i-l">备注名</p>
                                <i-input v-model="userInfo.memoName" class="user-info-c-i-r"></i-input>
                            </div>
                            <div v-if="false" class="user-info-c-i">
                                <p class="user-info-c-i-l">视讯回水方案</p>
                                <i-select v-model="userInfo.videoLevel" class="user-info-c-i-r">
                                    <i-option v-for="(item, index) in userVideoLevelList" :key="index" :value="item.value">{{item.title}}</i-option>
                                </i-select>
                            </div>
                            <div v-show="!userInfo.isFalse" class="user-info-c-i">
                                <p class="user-info-c-i-l">登录密码</p>
                                <i-input v-model="userInfo.password" placeholder="修改登录密码填，不修改留空" class="user-info-c-i-r"></i-input>
                            </div>
                            <div class="user-info-c-i">
                                <p class="user-info-c-i-l">群信息显示</p>
                                <RadioGroup v-model="userInfo.showType" style="margin-left: 8px;">
                                    <Radio label="nickname" :disabled="!userInfo.memoName">
                                        <span>昵称</span>
                                    </Radio>
                                    <Radio label="remark" :disabled="!userInfo.memoName">
                                        <span>备注名</span>
                                    </Radio>
                                </RadioGroup>
                            </div>
                        </div>
                    </div>
                    <div class="operate">
                        <span @click="handleSubmit">确定</span>
                        <span @click="handleCloseModal('showUserInfo')">取消</span>
                    </div>
                </div>
            </div>
            <div v-show="showLotteryRecode" class="lottery-recode">
                <div class="user-info-header">
                    <span>{{lotteryTitle}}</span>
                    <span class="modal-close" @click="handleCloseModal('showLotteryRecode')">X</span>
                </div>
                <div class="lottery-recode-c">
                    <div v-show="lotteryType === 'history'" class="recode-search">
                        <DatePicker v-model="searchDate" type="datetimerange" :options="forbiddenTime" format="yyyy-MM-dd HH:mm" placeholder="选择搜索的时间" style="width: 420px"></DatePicker>
                        <span>期数：</span>
                        <i-input placeholder="期数搜索" v-model="searchPeriod" style="width: 80px;"></i-input>
                        <i-button @click="handleSearchLottery">搜索</i-button>
                    </div>
                    <i-table :loading="childPage.isLoading" height="514" :columns="lotteryCols" :data="lotteryList"></i-table>
                    <Page class="footer-page" :total="childPage.total" :page-size="childPage.pageSize" :current="childPage.pageNum" show-sizer @on-page-size-change="handleChangeChildSize" @on-change="handleChangeChildPage"/>
                </div>
            </div>
            <div v-show="showBranchRecode" class="branch-recode">
                <div class="user-info-header">
                    <span>{{lotteryTitle}}</span>
                    <span class="modal-close" @click="handleCloseModal('showBranchRecode')">X</span>
                </div>
                <div class="list-header">
                    <div class='list-title'>
                        <span>{{lotteryTitle}}</span>
                        <div v-show="type === 'score'" class="list-info">
                            <span>上分合计：</span>
                            <span>{{totalScore}}</span>
                            <span>下分合计：</span>
                            <span>{{totalReturn}}</span>
                        </div>
                    </div>
                </div>
                <div v-show="lotteryType === 'history'" class="recode-search">
                    <DatePicker v-model="searchDate" type="datetimerange" :options="forbiddenTime" format="yyyy-MM-dd HH:mm" placeholder="选择搜索的时间" style="width: 420px"></DatePicker>
                    <i-button @click="handleSearchLottery">搜索</i-button>
                </div>
                <div>
                    <i-table :loading="childPage.isLoading" height="510" class="table-style" :columns="recodeCols" :data="recodeList"></i-table>
                    <Page v-show="type === 'account'" class="footer-page" :total="childPage.total" :page-size="childPage.pageSize" :current="childPage.pageNum" show-sizer @on-page-size-change="handleChangeChildSize" @on-change="handleChangeChildPage"/>
                </div>
            </div>
            <div v-show="showAccount" class="branch-recode">
                <div class="user-info-header">
                    <span>{{lotteryTitle}}</span>
                    <span class="modal-close" @click="handleCloseModal('showAccount')">X</span>
                </div>
                <i-table :loading="childPage.isLoading" :columns="accountInfoCols" :data="accountInfoList"></i-table>
                <p style="padding: 20px;color: red;">点击图片显示原图</p>
                <div v-show="qrcodeIcon" class="show-qrcode" @click="hiddenQrcode">
                    <img style="max-width: 100%; max-height: 100%;" :src="qrcodeIcon" alt title/>
                    <p style="padding: 20px;color: #fff;">点击隐藏图片</p>
                </div>
            </div>
        </div>
        <Modal v-model="showDelete" class-name="vertical-center-modal" width="300">
            <p slot="header" style="color: #0d1941;font-size: 16px;text-align:center">
                <span>确认删除</span>
            </p>
            <p style="text-align:center">是否确认删除？</p>
            <div slot="footer" class="modal-footer">
                <div class="enter-cancel" @click="showDelete = false">取消</div>
                <div class="enter-ok" @click="handleEnterDelete">确定</div>
            </div>
        </Modal>
        <Modal v-model="showCancelAgent" class-name="vertical-center-modal" width="300">
            <p slot="header" style="color: #0d1941;font-size: 16px;text-align:center">
                <span>{{poptipText}}</span>
            </p>
            <p style="text-align:center">{{'是否确定' + poptipText + '?'}}</p>
            <div slot="footer" class="modal-footer">
                <div class="enter-cancel" @click="showCancelAgent = false">取消</div>
                <div class="enter-ok" @click="handleEnter">确定</div>
            </div>
        </Modal>
        <my-upload @crop-success="handleCropSuccess"
            v-model="showUploadHeader"
            :width="100"
            :height="100"
            img-format="png"></my-upload>
        <!-- <loading :loading="showLoading"></loading> -->
    </div>
</template>

<script>
import switchs from '../../component/switch.vue';
import myUpload from 'vue-image-crop-upload';
export default {
    components: {
        switchs,
        myUpload
    },
    data() {
        let that = this;
        return {
            qrcodeIcon: '',
            showUploadHeader: false,
            poptipText: '',
            clientHeight: document.body.clientHeight,
            gameList: [
                {
                    title: '所有游戏',
                    value: '0'
                },
                {
                    title: '北京赛车',
                    value: '1'
                },
                {
                    title: '幸运飞艇',
                    value: '2'
                },
                {
                    title: '重庆时时彩',
                    value: '3'
                },
                {
                    title: '极速赛车',
                    value: '4'
                },
                {
                    title: '澳洲10',
                    value: '5'
                },
                {
                    title: '澳洲5',
                    value: '6'
                }
            ],
            videoGameList: [
                {
                    title: '所有游戏',
                    value: '0'
                },
                {
                    title: '百家乐',
                    value: '1'
                },
                {
                    title: '龙虎',
                    value: '2'
                },
                {
                    title: '牛牛',
                    value: '3'
                }
            ],
            showLoading: false,
            searchUsername: '',
            selectUserType: 0,
            userInfo: {
                id: '',
                speakingStatus: 'false',
                usrename: '',
                loginTime: '',
                nickname: '',
                accountBalance: 0,
                password: '',
                level: 1,
                videoLevel: 1,
                remark: '',
                memoName: '',
                showType: '',
                OpenID: ''
            },
            showDelete: false,
            page: {
                total: 0,
                pageSize: 10,
                pageNum: 1
            },
            childPage: {
                total: 0,
                pageSize: 10,
                pageNum: 1,
                isLoading: false
            },
            userLevelList: [],
            userVideoLevelList: [],
            userSpeakList: [
                {
                    title: '禁止',
                    status: 'false'
                },
                {
                    title: '开放',
                    status: 'true'
                }
            ],
            lotteryTitle: '',
            lotteryType: '',
            searchDate: [new Date().pattern('yyyy-MM-dd') + ' 00:00', new Date().pattern('yyyy-MM-dd HH:mm')],
            searchPeriod: '',
            selectItem: {},
            showCancelAgent: false,
            userTypeList: [
                {
                    title: '所有用户',
                    value: 0
                },
                {
                    title: '正式用户',
                    value: '1'
                },
                {
                    title: '虚假用户',
                    value: '2'
                },
                {
                    title: '封禁踢人',
                    value: '3'
                }
            ],
            columnListShow: [
                {
                    title: '赛车盈利',
                    value: 'racing'
                },
                {
                    title: '飞艇盈利',
                    value: 'airship'
                },
                {
                    title: '时时彩盈利',
                    value: 'timeHonored'
                },
                {
                    title: '极速盈利',
                    value: 'extremeSpeed'
                },
                {
                    title: '澳10盈利',
                    value: 'australiaTen'
                },
                {
                    title: '澳5盈利',
                    value: 'australiaFive'
                },
                {
                    title: '上下分记录',
                    value: 'records'
                },
                {
                    title: '账变记录',
                    value: 'accountChange'
                },
                {
                    title: '操作',
                    value: 'operate'
                }
            ],
            branchCols: [
                {
                    title: '用户',
                    key: 'usrename'
                },
                {
                    title: '金额',
                    key: 'balance'
                },
                {
                    title: '申请时间',
                    key: 'time'
                },
                {
                    title: '上分/下分',
                    key: 'status',
                    width: 200
                }
            ],
            accountCols: [
                {
                    title: '时间',
                    key: 'time',
                    width: 110
                },
                {
                    title: '变动金额',
                    key: 'money',
                    width: 120
                },
                {
                    title: '变动后余额',
                    key: 'balance',
                    width: 120
                },
                {
                    title: '信息',
                    render: (h, params) => {
                        return h('div', {
                            style: {
                                'word-break': 'break-word'
                            }
                        }, params.row.info);
                    }
                },
                {
                    title: '备注',
                    key: 'remark'
                }
            ],
            recodeCols: [],
            recodeList: [],
            lotteryCols: [
                {
                    title: '期号',
                    width: 120,
                    key: 'id'
                },
                {
                    title: '投注金额',
                    width: 100,
                    key: 'betting'
                },
                {
                    title: '投注类型',
                    minWidth: 100,
                    key: 'type'
                },
                {
                    title: '盈亏',
                    width: 100,
                    key: 'profitLoss'
                },
                {
                    title: '中奖号码',
                    minWidth: 160,
                    key: 'winningNum'
                },
                {
                    title: '开奖号码',
                    minWidth: 160,
                    key: 'lotteryNum'
                },
                {
                    title: '开奖时间',
                    minWidth: 160,
                    key: 'lotteryTime'
                }
            ],
            lotteryList: [],
            userData: [],
            showUserInfo: false,
            showLotteryRecode: false,
            showBranchRecode: false,
            showAccount: false,
            accountInfoCols: [
                {
                    title: '账户号码',
                    key: 'id',
                    align: 'center'
                },
                {
                    title: '二维码',
                    align: 'center',
                    render: (h, params) => {
                        return h('div', [
                            h('img', {
                                style: {
                                    width: '40px',
                                    height: '40px',
                                    cursor: 'pointer'
                                },
                                attrs: {
                                    src: params.row.icon
                                },
                                on: {
                                    click: () => {
                                        that.showQrcode(params.row.icon);
                                    }
                                }
                            })
                        ]);
                    }
                },
                {
                    title: '账户类型',
                    key: 'type',
                    align: 'center'
                }
            ],
            accountInfoList: [],
            forbiddenTime: {
                disabledDate (date) {
                    return date && date.valueOf() >= Date.now();
                }
            },
            isLoading: false,
            type: '',
            gameType: '',
            imgDataUrl: '',
            memberNum: 0
        }
    },
    mounted() {
        let that = this;
        that.getData();
        that.handleMethodsList();
        that.handleVideoMethodsList();
        that.handleGetExistCount();
    },
    computed: {
        totalScore() {
            let that = this;
            return that.recodeList.reduce((total, item) => {
                return total + ((item.statusCode == 1 && item.type == 1) ? parseInt(item.balance.match(/\d.*/g)[0]) : 0)
            }, 0)
        },
        totalReturn() {
            let that = this;
            return that.recodeList.reduce((total, item) => {
                return total + ((item.statusCode == 1 && item.type == 2) ? parseInt(item.balance.match(/\d.*/g)[0]) : 0)
            }, 0)
        }
    },
    methods: {
        handleVerifyAccount() {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/User/GenerateAccount',
                    method: 'post',
                    params: {
                        userID: that.userInfo.id,
                        loginName: that.handleCreateAccount()
                    }
                }).then(function(data) {
                    that.isLoading = false;
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                        that.handleSettingUser(that.userInfo);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                })
            }
        },
        handleCreateAccount() {
            let wordList = ['a','b','c','d','e','f','g','h','i','j','k','l','m','n','o','p','q','r','s','t','u','v','w','x','y','z'];
            let wordRandom = Math.floor(Math.random() * 26);
            let account = wordList[wordRandom];
            for (let i = 0; i <= 5; i++) {
                account += Math.floor(Math.random() * 9);
            }
            return account;
        },
        handleEnter() {
            let that = this;

            switch(that.type) {
                case 'agent':
                    that.handleActAsAgent();
                break;
                case 'status':
                    that.handleChangeStatus();
                break;
            }
        },
        handleImageError(item) {
            if (!item['error']) {
                item.header = item.header.split('?')[0] + `?time=${new Date().getTime()}`
                item['error'] = 1;
            } else {
                item.header = '/UserImages/default.png';
            }
        },
        handleCropSuccess(imgDataUrl, field) {
            let that = this;

            that.imgDataUrl = imgDataUrl;
            console.log(imgDataUrl, field)
        },
        handleUploadIcon() {
            let that = this,
                formData = new FormData(),
                file = that.dataURItoFile(that.imgDataUrl);

            if (file.size > 5 * 1024 * 1024) {
                that.$Message.error('上传图片不能超过5M');
                return;
            }
            if (!that.isLoading) {
                that.isLoading = true;

                formData.append('fileinput', file);
                that.$axios({
                    url: '/api/User/UpdateUserImages',
                    method: 'post',
                    params: {
                        userID: that.userInfo.id
                    },
                    data: formData
                }).then(function(data) {
                    that.isLoading = false;
                    that.showUserInfo = false;
                    that.imgDataUrl = '';
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                        that.getData();
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                })
            }
        },
        dataURItoFile(base64Data) {
            var byteString;
            if(base64Data.split(',')[0].indexOf('base64') >= 0)
                byteString = atob(base64Data.split(',')[1]);
            else
                byteString = unescape(base64Data.split(',')[1]);
            var mimeString = base64Data.split(',')[0].split(':')[1].split(';')[0];
            var ia = new Uint8Array(byteString.length);
            for(var i = 0; i < byteString.length; i++) {
                ia[i] = byteString.charCodeAt(i);
            }
            return new File([ia], 'upload.png', {
                type: mimeString
            });
        },
        showQrcode(href) {
            let that = this;

            that.qrcodeIcon = href;
        },
        hiddenQrcode() {
            let that = this;

            that.qrcodeIcon = '';
        },
        handleMethodsList() {
            let that = this;

            that.$axios({
                url: '/api/User/GetGrogrammeList'
            }).then(function(data) {
                if (data.data.Status === 100) {
                    const result = data.data.Data;

                    that.userLevelList = result.map(item => {
                        return {
                            value: item.ID,
                            title: item.Name + '-' + that.gameList.find(ele => (item.GameType || '0') == ele.value).title
                        }
                    })
                }
            })
        },
        handleGetExistCount() {
            let that = this;

            that.$axios({
                url: '/api/User/GetExistingCount'
            }).then(function(data) {
                if (data.data.Status === 100) {
                    that.memberNum = data.data.Keyword;
                }
            })
        },
        handleVideoMethodsList() {
            let that = this;

            that.$axios({
                url: '/api/User/GetVideoGrogrammeList'
            }).then(function(data) {
                if (data.data.Status === 100) {
                    const result = data.data.Data;

                    that.userVideoLevelList = result.map(item => {
                        return {
                            value: item.ID,
                            title: item.Name + '-' + that.videoGameList.find(ele => (item.GameType || '0') == ele.value).title
                        }
                    })
                }
            })
        },
        handleSearch() {
            let that = this;
            that.page.pageNum = 1;
            that.getData();
        },
        handleChangeType(item) {
            let that = this;
            that.page.pageNum = 1;
            that.getData();
        },
        handleSettingUser(item) {
            let that = this;

            that.$axios({
                url: '/api/User/GetUserInfo',
                params: {
                    userID: item.id
                }
            }).then(function(data) {
                if (data.data.Status === 100) {
                    const result = data.data.Model;

                    that.userInfo = {
                        id: result.ID,
                        showId: result.OnlyCode,
                        speakingStatus: result.Talking.toString(),
                        usrename: result.LoginName,
                        loginTime: result.LoginTime,
                        nickname: result.NickName || item.nickname,
                        accountBalance: result.UserMoney,
                        password: result.Password,
                        level: result.ProgrammeID,
                        videoLevel: result.VideoProgrammeID,
                        remark: result.Remark,
                        header: item.header,
                        status: item.status,
                        isFalse: item.isFalse,
                        memoName: result.MemoName,
                        showType: result.ShowType ? 'nickname' : 'remark',
                        OpenID: result.OpenID
                    }
                    that.showUserInfo = true;
                } else {
                    that.$Message.error(data.data.Message)
                }
            }).catch(function() {
                that.$Message.error('查询用户信息失败');
            })
        },
        handleCloseModal(key) {
            const that = this;
            that.childPage.total = 0;
            that.childPage.pageNum = 1;
            that.childPage.pageSize = 10;
            that.imgDataUrl = '';
            that[key] = !that[key];
        },
        handleSearchLottery() {
            let that = this;

            if (that.showBranchRecode || that.showLotteryRecode) {
                switch(that.type) {
                    case 'score':
                        that.getBranchRecodeList();
                    break;
                    case 'account':
                        that.getAccountHistory();
                    break;
                    case 'game':
                        that.getGameData();
                    break;
                }
            }
        },
        handleRacingToday(item) {
            let that = this;
            that.lotteryTitle = item.column.title;
            that.userInfo = item.row;
            that.lotteryType = 'today';
            that.gameType = 1;
            that.type = 'game';
            that.getGameData();
            that.showLotteryRecode = true;
        },
        handleRacingHistory(item) {
            let that = this;
            that.lotteryTitle = item.column.title;
            that.userInfo = item.row;
            that.lotteryType = 'history';
            that.gameType = 1;
            that.type = 'game';
            that.getGameData();
            that.showLotteryRecode = true;
        },
        handleAirshipToday(item) {
            let that = this;
            that.lotteryTitle = item.column.title;
            that.userInfo = item.row;
            that.lotteryType = 'today';
            that.gameType = 2;
            that.type = 'game';
            that.getGameData();
            that.showLotteryRecode = true;
        },
        handleAirshipHistory(item) {
            let that = this;
            that.lotteryTitle = item.column.title;
            that.userInfo = item.row;
            that.lotteryType = 'history';
            that.gameType = 2;
            that.type = 'game';
            that.getGameData();
            that.showLotteryRecode = true;
        },
        handleTimeToday(item) {
            let that = this;
            that.lotteryTitle = item.column.title;
            that.userInfo = item.row;
            that.lotteryType = 'today';
            that.gameType = 3;
            that.type = 'game';
            that.getGameData();
            that.showLotteryRecode = true;
        },
        handleTimeHistory(item) {
            let that = this;
            that.lotteryTitle = item.column.title;
            that.userInfo = item.row;
            that.lotteryType = 'history';
            that.gameType = 3;
            that.type = 'game';
            that.getGameData();
            that.showLotteryRecode = true;
        },
        handleSpeedToday(item) {
            let that = this;
            that.lotteryTitle = item.column.title;
            that.userInfo = item.row;
            that.lotteryType = 'today';
            that.gameType = 4;
            that.type = 'game';
            that.getGameData();
            that.showLotteryRecode = true;
        },
        handleSpeedHistory(item) {
            let that = this;
            that.lotteryTitle = item.column.title;
            that.userInfo = item.row;
            that.lotteryType = 'history';
            that.gameType = 4;
            that.type = 'game';
            that.getGameData();
            that.showLotteryRecode = true;
        },
        handleTenToday(item) {
            let that = this;
            that.lotteryTitle = item.column.title;
            that.userInfo = item.row;
            that.lotteryType = 'today';
            that.gameType = 5;
            that.type = 'game';
            that.getGameData();
            that.showLotteryRecode = true;
        },
        handleTenHistory(item) {
            let that = this;
            that.lotteryTitle = item.column.title;
            that.userInfo = item.row;
            that.lotteryType = 'history';
            that.gameType = 5;
            that.type = 'game';
            that.getGameData();
            that.showLotteryRecode = true;
        },
        handleFiveToday(item) {
            let that = this;
            that.lotteryTitle = item.column.title;
            that.userInfo = item.row;
            that.lotteryType = 'today';
            that.gameType = 6;
            that.type = 'game';
            that.getGameData();
            that.showLotteryRecode = true;
        },
        handleFiveHistory(item) {
            let that = this;
            that.lotteryTitle = item.column.title;
            that.userInfo = item.row;
            that.lotteryType = 'history';
            that.gameType = 6;
            that.type = 'game';
            that.getGameData();
            that.showLotteryRecode = true;
        },
        getGameData() {
            let that = this,
                param = {};

            if (that.lotteryType === 'today') {
                param['startTime'] = new Date().pattern('yyyy-MM-dd') + ' 00:00';
                param['endTime'] = new Date().pattern('yyyy-MM-dd HH:mm');
            } else {
                param['startTime'] = that.searchDate[0];
                param['endTime'] = that.searchDate[1];
            }

            param['userID'] = that.userInfo.id;
            param['gameType'] = that.gameType;
            if (that.searchPeriod) {
                param['nper'] = that.searchPeriod;
            }
            param['start'] = that.childPage.pageNum;
            param['pageSize'] = that.childPage.pageSize;
            that.lotteryList = [];

            if (!that.childPage.isLoading) {
                that.childPage.isLoading = true;

                that.$axios({
                    url: '/api/User/GetUserGameInfos',
                    params: param
                }).then(function(data) {
                    that.childPage.isLoading = false;
                    if (data.data.Status === 100) {
                        const result = data.data.Data;

                        that.childPage.total = data.data.Total;
                        that.lotteryList = result.map(item => {
                            return {
                                id: item.Nper,
                                betting: item.AllBet,
                                type: item.BetType,
                                profitLoss: item.Loss,
                                winningNum: item.WinNums,
                                lotteryNum: item.LotNums,
                                lotteryTime: item.LotTime
                            }
                        })
                    }
                })
            }
        },
        handleScoreToday(item) {
            let that = this;
            that.recodeCols = that.branchCols;
            that.lotteryTitle = '上下分记录';
            that.userInfo = item;
            that.lotteryType = 'today';
            that.type = 'score';
            that.showBranchRecode = true;
            that.getBranchRecodeList();
        },
        handleScoreHistory(item) {
            let that = this;
            that.recodeCols = that.branchCols;
            that.lotteryTitle = '上下分记录';
            that.userInfo = item;
            that.searchDate = [new Date().pattern('yyyy-MM-dd') + ' 00:00', new Date().pattern('yyyy-MM-dd') + ' 23:59'];
            that.lotteryType = 'history';
            that.showBranchRecode = true;
            that.type = 'score';
            that.getBranchRecodeList();
        },
        getBranchRecodeList() {
            let that = this,
                param = {};

            if (!that.childPage.isLoading) {
                that.childPage.isLoading = true;

                if (that.lotteryType === 'today') {
                    param['startTime'] = new Date().pattern('yyyy-MM-dd') + ' 00:00';
                    param['endTime'] = new Date().pattern('yyyy-MM-dd HH:mm');
                } else {
                    param['startTime'] = that.searchDate[0];
                    param['endTime'] = that.searchDate[1];
                }

                param['userID'] = that.userInfo.id;
                that.recodeList = [];

                that.$axios({
                    url: '/api/User/GetUserBrancHis',
                    params: param
                }).then(function(data) {
                    that.childPage.isLoading = false;
                    if (data.data.Status === 100) {
                        const result = data.data.Data;

                        that.recodeList = result.map(item => {
                            return {
                                usrename: item.NickName + '(' + item.OnlyCode + ')',
                                balance: item.Amount,
                                time: item.ApplyTime,
                                status: item.Message,
                                type: item.ChangeType,
                                statusCode: (item.Message.match('成功') || item.Message.match('系统')) ? 1 : 2
                            }
                        })
                    }
                })
            }
        },
        handleAccountToday(item) {
            let that = this;
            that.recodeCols = that.accountCols;
            that.lotteryTitle = '账变记录';
            that.userInfo = item;
            that.lotteryType = 'today';
            that.type = 'account';
            that.showBranchRecode = true;
            that.getAccountHistory();
        },
        handleAccountHistory(item) {
            let that = this;
            that.recodeCols = that.accountCols;
            that.lotteryTitle = '账变记录';
            that.userInfo = item;
            that.searchDate = [new Date().pattern('yyyy-MM-dd') + ' 00:00', new Date().pattern('yyyy-MM-dd') + ' 23:59'];
            that.lotteryType = 'history';
            that.type = 'account';
            that.showBranchRecode = true;
            that.getAccountHistory();
        },
        getAccountHistory() {
            let that = this,
                param = {};

            if (!that.childPage.isLoading) {
                that.childPage.isLoading = true;

                if (that.lotteryType === 'today') {
                    param['startTime'] = new Date().pattern('yyyy-MM-dd') + ' 00:00';
                    param['endTime'] = new Date().pattern('yyyy-MM-dd') + ' 23:59';
                } else {
                    param['startTime'] = that.searchDate[0];
                    param['endTime'] = that.searchDate[1];
                }

                param['userID'] = that.userInfo.id;
                param['start'] = that.childPage.pageNum;
                param['pageSize'] = that.childPage.pageSize;
                that.recodeList = [];

                that.$axios({
                    url: '/api/User/GetUserAccountChange',
                    params: param
                }).then(function(data) {
                    that.childPage.isLoading = false;
                    if (data.data.Status === 100) {
                        const result = data.data.Data;

                        that.childPage.total = data.data.Total;
                        that.recodeList = result.map(item => {
                            return {
                                time: item.ApplyTime,
                                money: item.Amount,
                                balance: item.Balance,
                                info: item.Message,
                                remark: item.Remark
                            }
                        })
                    }
                })
            }
        },
        handleChangeFlyingSheet(item) {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/User/RecordOperation',
                    params: {
                        userID: item.id
                    }
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                    that.isLoading = false;
                    that.getData();
                })
            }
        },
        handleChange(item) {
            let that = this;

            if (item.isFalse) {
                return;
            }
            that.poptipText = item.status === '假人' ? '切换真人' : '切换假人';
            that.type = 'status';
            that.selectItem = item;
            that.showCancelAgent = true;
        },
        handleChangeStatus() {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/User/DummyOperation',
                    params: {
                        userID: that.selectItem.id
                    }
                }).then(function(data) {
                    that.showCancelAgent = false;
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                    that.isLoading = false;
                    that.getData();
                })
            }
        },
        handleFreezing(item) {
            let that = this;

            if ((item.status === '正常' || item.status === '冻结')) {
                if (!that.isLoading) {
                    that.isLoading = true;

                    that.$axios({
                        url: '/api/User/FrozenOperation',
                        params: {
                            userID: item.id
                        }
                    }).then(function(data) {
                        if (data.data.Status === 100) {
                            that.$Message.success(data.data.Message);
                        } else {
                            that.$Message.error(data.data.Message);
                        }
                        that.isLoading = false;
                        that.getData();
                    })
                }
            }
        },
        handleDelete(item) {
            let that = this;

            that.userInfo.id = item.id;
            that.showDelete = true;
        },
        handleEnterDelete() {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/User/DeleleUser',
                    method: 'post',
                    params: {
                        userID: that.userInfo.id
                    }
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                    that.isLoading = false;
                    that.showDelete = false;
                    that.page.pageNum = 1;
                    that.getData();
                })
            }
        },
        handleCollecAcction(item) {
            let that = this;

            if (item.status === '正常') {
                that.handleGetAcction(item.id)
                that.lotteryTitle = '下分账户';
                that.showAccount = true;
            }
        },
        handleGetAcction(id) {
            let that = this;

            if (!that.childPage.isLoading) {
                that.childPage.isLoading = true;

                that.$axios({
                    url: '/api/User/GetUserReceivables',
                    params: {
                        userID: id
                    }
                }).then(function(data) {
                    that.childPage.isLoading = false;
                    that.accountInfoList = [];

                    if (data.data.Status === 100) {
                        let wechat = data.data.Model.WeChatUrl;
                        let ali = data.data.Model.AlipayUrl;
                        if (data.data.Model.WeChat) {
                            that.accountInfoList.push({
                                id: data.data.Model.WeChat,
                                type: '微信',
                                icon: data.data.Model.WeChat ? (wechat.indexOf('://') === -1 ? ('/' + wechat) : wechat) : '无'
                            })
                        }

                        if (data.data.Model.Alipay) {
                            that.accountInfoList.push({
                                id: data.data.Model.Alipay,
                                type: '支付宝',
                                icon: data.data.Model.Alipay ? (ali.indexOf('://') === -1 ? ('/' + ali) : ali) : '无'
                            })
                        }
                    }
                })
            }
        },
        handleAgent(item) {
            let that = this;

            if (item.isAgent) {
                that.selectItem = item;
                that.poptipText = '取消代理';
                that.type = 'agent';
                that.showCancelAgent = true;
            } else {
                that.handleActAsAgent(item);
            }
        },
        handleActAsAgent(it) {
            let that = this,
                item = it;

            if (!item) {
                item = that.selectItem;
            }

            if (item.status === '正常') {
                if (!that.isLoading) {
                    that.isLoading = true;

                    that.$axios({
                        url: '/api/User/AvatarOperation',
                        params: {
                            userID: item.id
                        }
                    }).then(function(data) {
                        if (data.data.Status === 100) {
                            that.showCancelAgent = false;
                            that.$Message.success(data.data.Message);
                        } else {
                            that.$Message.error(data.data.Message);
                        }
                        that.isLoading = false;
                        that.getData();
                    })
                }
            }
        },
        handleSubmit() {
            let that = this;

            if (!that.userInfo.nickname) {
                that.$Message.error('请填写用户昵称');
                return;
            }

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/User/UpdateUserInfo',
                    method: 'post',
                    data: {
                        'ID': that.userInfo.id,
                        'Talking': that.userInfo.speakingStatus === 'true' ? true : false,
                        'UserMoney': that.userInfo.accountBalance,
                        'Remark': that.userInfo.remark,
                        'Password': that.userInfo.password || '',
                        'NickName': that.userInfo.nickname,
                        'ProgrammeID': that.userInfo.level,
                        'VideoProgrammeID': that.userInfo.videoLevel,
                        'MemoName': that.userInfo.memoName,
                        'ShowType': that.userInfo.showType === 'nickname'
                    }
                }).then(function(data) {
                    that.isLoading = false;
                    if (data.data.Status === 100) {
                        if (that.imgDataUrl) {
                            that.handleUploadIcon();
                        } else {
                            that.$Message.success(data.data.Message);
                            that.showUserInfo = false;
                            that.getData();
                        }
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                })
            }
        },
        handleChangeChildSize(_pageSize) {
            let that = this;

            that.childPage.pageSize = _pageSize;
            that.childPage.pageNum = 1;
            that.handleSearchLottery();
        },
        handleChangeChildPage(page) {
            let that = this;

            that.childPage.pageNum = page;
            that.handleSearchLottery();
        },
        handleChangeSize(_pageSize) {
            let that = this;

            that.page.pageSize = _pageSize;
            that.page.pageNum = 1;
            that.getData();
        },
        handleChangePage(page) {
            let that = this;

            that.page.pageNum = page;
            that.getData();
        },
        getData() {
            let that = this,
                param = {}

            if (that.searchUsername) {
                param['loginName'] = that.searchUsername;
            }

            if (that.selectUserType) {
                switch(that.selectUserType) {
                    case '0':
                    break;
                    case '1':
                        param['status'] = 1; // 正常用户
                    break;
                    case '2':
                        param['status'] = 4; // 假人
                    break;
                    case '3':
                        param['status'] = 2; // 冻结
                    break;
                }
            }

            param['start'] = that.page.pageNum;
            param['pageSize'] = that.page.pageSize;

            if (!that.isLoading) {
                that.isLoading = true;
                that.showLoading = true;
                that.$axios({
                    url: '/api/User/SearchUserInfo',
                    params: param
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        const result = data.data.Data;

                        that.page.total = data.data.Total;

                        that.userData = result.map(item => {
                            return {
                                id: item.ID,
                                showId: item.OnlyCode,
                                header: item.Avatar ? (item.Avatar.indexOf('://') != -1 ? item.Avatar : (('/' + item.Avatar) + `?time=${new Date().getTime()}`)) : '',
                                username: item.NickName,
                                nickname: item.NickName,
                                loginName: item.LoginName,
                                balance: item.UserMoney,
                                isAgent: item.IsAgent,
                                record: item.Record, // 飞单状态
                                status: item.Status,
                                proLoss: item.ProLoss,
                                betGameName: item.BetGameName,
                                isFalse: item.IsSupport
                            }
                        })
                    }
                    that.isLoading = false;
                    that.showLoading = false;
                })
            }
        }
    },
}
</script>

